<template>
  <div class="container">
    <a-row :gutter="[24]" v-if="userInfo">
      <a-col
        :xs="{ span: 24, offset: 0 }"
        :md="{ span: 22, offset: 1 }"
        :lg="{ span: 22, offset: 1 }"
        :xl="{ span: 22, offset: 1 }"
        class="center"
      >
        <div class="head">
          <div class="head__photo">
            <v-icon name="camera"></v-icon>
          </div>
          <div class="head__info">
            <h2>{{getFullName}}</h2>
            <div class="head__info__indicators">
              <span>{{getAge}} лет</span>
              <span class="points">{{userInfo.district}}</span>
              <span>{{userInfo.course_education}} курс</span>
            </div>
            <router-link to="/profile/edit" class="button-link">
              <a-button
                type="info"
                block
                html-type="submit"
                class="login-form-button"
              >
                Редактировать анкету
              </a-button>
            </router-link>
          </div>
        </div>
        <profile-steps
        currentStep="Участник очного этапа">
        </profile-steps>
        <profile-nomination></profile-nomination>
        <profile-infoblock
        v-for="(block, key) in infos"
        :key="key"
        :userInfo="userInfo"
        :data="block.info"
        :title="block.title">
        </profile-infoblock>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import ProfileSteps from '@/components/Student/ProfileSteps.vue'
import ProfileNomination from '@/components/Student/ProfileNomination.vue'
import ProfileInfoblock from '@/components/Student/ProfileInfoblock.vue'
import moment from 'moment'
export default {
  components: { ProfileSteps, ProfileNomination, ProfileInfoblock },
  data() {
    return {
      infos: [
        {
          title: 'Личные данные',
          info: [
            {name: 'Пол', key: 'gender', render: value => this.getGender(value) },
            { name: 'Дата рождения', key: 'date_birth', render: value => this.getBirthday(value) },
            { name: 'Номер телефона', key: 'phone_number' },
            { name: 'E-mail', key: 'email' },
            { name: 'Гражданство', key: 'citizenship' },
          ]
        },
        {
          title: 'Адрес',
          info: [
            {name: 'Регион', key: 'region' },
            { name: 'Район/городской округ', key: 'locality', render: value => this.getBirthday(value) },
            { name: 'Почтовый индекс', key: 'postal_code' },
            { name: 'Улица', key: 'street' },
            { name: 'Номер дома', key: 'apartment' },
            // { name: 'Квартира', key: 'apartment' },
          ]
        },
        {
          title: 'Образование',
          info: [
            {name: 'Форма обучения', key: 'form_education' },
            { name: 'Место обучения', key: 'place_education' },
            // { name: 'Образовательная организация', key: 'phone' },
          ]
        },
      ]
    }
  },
  methods: {
    getGender(gender) {
      return gender === 'male' ? 'Мужской' : 'Женский'
    },
    getBirthday(date) {
      return date.split('-').reverse().join('-')
    }
  },
  computed: {
    userInfo() {
      return this.$store.state.user.userInfo
    },
    getFullName() {
      const {first_name, last_name, patronymic} = this.userInfo
      return `${first_name} ${last_name} ${patronymic}`
    },
    getAge() {
      return moment().diff(this.userInfo.date_birth, 'years')
    }
  }
}
</script>

